.cart_section {
  position: relative;
  top: 60px;
  width: 100%;
  /* height: 100vh; */
  /* background-color: rgb(245, 239, 239); */
  text-align: justify;
}

.cart_section .cart_container {
  width: 95%;
  margin: 0 auto;
  display: flex;
  padding: 50px 20px;
  justify-content: space-between;
}

.cart_container .left_cart {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* background: #f2f2f2;
  border: 1px #c4c4c4 solid; */
  border-radius: 10px;
}

.left_cart img {
  /* width: 50%; */
  margin-bottom: 30px;
}

.left_cart .cart_btn {
  display: flex;
  justify-content: flex-start;
}

.cart_btn button {
  margin-right: 20px;
  padding: 10px 30px;
  cursor: pointer;
}
.cart_btn .cart_btn1 {
  background-color: #ffd814;
  border: none;
  outline: none;
  border-radius: 20px;
  color: #0f1111;
  width: 200px;
  letter-spacing: 0.5px;
  font-size: 14px;
  box-shadow: 0 0 10px -5px rgba(0, 0, 0, 0.5);
}

.cart_btn .cart_btn1:hover {
  background-color: #f1cc13;
}

.cart_btn .cart_btn2 {
  background-color: #ffa41c;
  letter-spacing: 0.5px;
  font-size: 14px;
  color: #0f1111;
  border: none;
  outline: none;
  border-radius: 20px;
  width: 200px;
  box-shadow: 0 0 10px -5px rgba(0, 0, 0, 0.5);
}

.cart_btn .cart_btn2:hover {
  background-color: #ec9615;
}

.cart_container .right_cart {
  flex: 0.5;
  border: 1px solid #c4c4c4;
  padding: 10px;
  border-radius: 10px;
  margin-left: 10px;
}

.right_cart h3 {
  font-weight: 500;
  font-size: 23px;
  margin-bottom: 5px;
  margin-top: 5px;
  margin-left: 5px;
}

.right_cart h4 {
  font-weight: 500;
  font-size: 20px;
  margin-bottom: 15px;
}

.right_cart .mrp {
  margin-top: 10px;
}

.right_cart p {
  color: #565959;
  font-size: 14px;
  margin-bottom: 8px;
}

.discount_box h5 {
  color: #b12704;
  font-weight: 500;

  font-size: 16px;
  margin-top: 0;
}

.discount_box h4 {
  font-size: 15px;
  margin-top: 10px;
  color: #007185;
}

.right_cart .description {
  font-weight: 600;
  color: #111;
  font-size: 16px;
  margin-top: 3px;
}

/* media query */

@media (max-width: 778px) {
  .cart_section {
    height: auto;
  }
  .cart_section .cart_container {
    flex-direction: column;
    align-items: center;
  }

  .cart_container .left_cart,
  .cart_container .right_cart {
    flex: 1;
  }
  .cart_container .left_cart {
    border: none;
    background: none;
  }
  .cart_container .right_cart {
    margin-top: 20px;
  }
}

@media (max-width: 600px) {
  .cart_btn .cart_btn1,
  .cart_btn .cart_btn2 {
    width: 160px;
  }

  .left_cart .cart_btn {
    align-items: center;
    justify-items: center;
    justify-content: space-around;
  }
  .cart_btn1 {
    margin-left: 10%;
    
  }
}
